import 'date-fns';
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { formValues, formTouched, formErrors } from 'components/__types__/alltywpsql';
import { withFormik } from 'formik';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { compose } from 'recompose';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import { DatePicker, MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import dateFnsLocales from 'i18n/dateFnsLocales';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import ConfirmationDialogTrigger from 'components/common/ConfirmationDialogTrigger';

const styles = (theme) => ({
  root: {
    margin: theme.spacing(3),
  },
  textField: {
    width: '100%',
  },
});
class AlltywpsqlForm extends PureComponent {
  constructor(props) {
    super(props);
    this.handleConfirmationDialogAction = this.handleConfirmationDialogAction.bind(this);
  }

  handleConfirmationDialogAction(action) {
    const { onDelete, values } = this.props;
    switch (action) {
      case ConfirmationDialogTrigger.CONFIRM: {
        onDelete(values);
        break;
      }
      default:
        break;
    }
  }

  render() {
    const {
      classes,
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit: formikHandleSubmit,
      onDelete,
      onCancelEditing,
      isSubmitting,
      setFieldValue,
      t,
      i18n,
    } = this.props;

    const handleDateChange = (field) => (value) => {
      setFieldValue(field, value);
    };

    const dateTimeLabelFn = (date) => (date ? new Date(date).toLocaleString(i18n.language) : '');
    const dateLabelFn = (date) => (date ? new Date(date).toLocaleDateString(i18n.language) : '');
    const getHelperText = (field) => (errors[field] && touched[field] ? errors[field] : '');

    const handleSubmit = (e) => {
      e.stopPropagation(); // avoids double submission caused by react-shadow-dom-retarget-events
      formikHandleSubmit(e);
    };

    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={dateFnsLocales[i18n.language]}>
        <form onSubmit={handleSubmit} className={classes.root} data-testid="alltywpsql-form">
          <Grid container spacing={2}>
            <input type="hidden" id="alltywpsql-id" data-testid="alltywpsql-id" value={values.id} />
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-stringa"
                error={errors.stringa && touched.stringa}
                helperText={getHelperText('stringa')}
                className={classes.textField}
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.stringa}
                name="stringa"
                label={t('entities.alltywpsql.stringa')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-intero"
                error={errors.intero && touched.intero}
                helperText={getHelperText('intero')}
                className={classes.textField}
                type="number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.intero}
                name="intero"
                label={t('entities.alltywpsql.intero')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-lungo"
                error={errors.lungo && touched.lungo}
                helperText={getHelperText('lungo')}
                className={classes.textField}
                type="number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.lungo}
                name="lungo"
                label={t('entities.alltywpsql.lungo')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-flotto"
                error={errors.flotto && touched.flotto}
                helperText={getHelperText('flotto')}
                className={classes.textField}
                type="number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.flotto}
                name="flotto"
                label={t('entities.alltywpsql.flotto')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-doppio"
                error={errors.doppio && touched.doppio}
                helperText={getHelperText('doppio')}
                className={classes.textField}
                type="number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.doppio}
                name="doppio"
                label={t('entities.alltywpsql.doppio')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="alltywpsql-grandecimale"
                error={errors.grandecimale && touched.grandecimale}
                helperText={getHelperText('grandecimale')}
                className={classes.textField}
                type="number"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.grandecimale}
                name="grandecimale"
                label={t('entities.alltywpsql.grandecimale')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <DatePicker
                id="alltywpsql-datalocale"
                error={errors.datalocale && touched.datalocale}
                helperText={getHelperText('datalocale')}
                className={classes.textField}
                onChange={handleDateChange('datalocale')}
                value={values.datalocale}
                labelFunc={dateLabelFn}
                name="datalocale"
                label={t('entities.alltywpsql.datalocale')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <DateTimePicker
                id="alltywpsql-istante"
                error={errors.istante && touched.istante}
                helperText={getHelperText('istante')}
                className={classes.textField}
                onChange={handleDateChange('istante')}
                value={values.istante}
                labelFunc={dateTimeLabelFn}
                name="istante"
                label={t('entities.alltywpsql.istante')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <DateTimePicker
                id="alltywpsql-zonadatatempo"
                error={errors.zonadatatempo && touched.zonadatatempo}
                helperText={getHelperText('zonadatatempo')}
                className={classes.textField}
                onChange={handleDateChange('zonadatatempo')}
                value={values.zonadatatempo}
                labelFunc={dateTimeLabelFn}
                name="zonadatatempo"
                label={t('entities.alltywpsql.zonadatatempo')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControlLabel
                control={
                  // eslint-disable-next-line react/jsx-wrap-multilines
                  <Checkbox
                    id="alltywpsql-booleano"
                    name="booleano"
                    onChange={handleChange}
                    inputProps={{ 'data-testid': 'alltywpsql-booleano-checkbox' }}
                    checked={values.booleano}
                    value="alltywpsql-booleano"
                    color="primary"
                  />
                }
                label={t('entities.alltywpsql.booleano')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <InputLabel htmlFor="alltywpsql-enumeratore">
                {t('entities.alltywpsql.enumeratore')}
              </InputLabel>
              <Select
                native
                id="alltywpsql-enumeratore"
                error={errors.enumeratore && touched.enumeratore}
                className={classes.textField}
                value={values.enumeratore}
                name="enumeratore"
                onChange={handleChange}
              >
                {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
                <option value="" />
                <option value="UNO">UNO</option>
                <option value="DUE">DUE</option>
                <option value="TRE">TRE</option>
                <option value="QUATTRO">QUATTRO</option>
                <option value="CINQUE">CINQUE</option>
              </Select>
            </Grid>
            {onDelete && (
              <ConfirmationDialogTrigger
                onCloseDialog={this.handleConfirmationDialogAction}
                dialog={{
                  title: t('entities.alltywpsql.deleteDialog.title'),
                  description: t('entities.alltywpsql.deleteDialog.description'),
                  confirmLabel: t('common.yes'),
                  discardLabel: t('common.no'),
                }}
                Renderer={({ onClick }) => (
                  <Button onClick={onClick} disabled={isSubmitting}>
                    {t('common.delete')}
                  </Button>
                )}
              />
            )}

            <Button onClick={onCancelEditing} disabled={isSubmitting} data-testid="cancel-btn">
              {t('common.cancel')}
            </Button>

            <Button type="submit" color="primary" disabled={isSubmitting} data-testid="submit-btn">
              {t('common.save')}
            </Button>
          </Grid>
        </form>
      </MuiPickersUtilsProvider>
    );
  }
}

AlltywpsqlForm.propTypes = {
  classes: PropTypes.shape({
    root: PropTypes.string,
    textField: PropTypes.string,
    submitButton: PropTypes.string,
    button: PropTypes.string,
    downloadAnchor: PropTypes.string,
  }),
  values: formValues,
  touched: formTouched,
  errors: formErrors,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  onCancelEditing: PropTypes.func,
  isSubmitting: PropTypes.bool.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({ language: PropTypes.string }).isRequired,
};

AlltywpsqlForm.defaultProps = {
  onCancelEditing: () => {},
  classes: {},
  values: {},
  touched: {},
  errors: {},
  onDelete: null,
};

const emptyAlltywpsql = {
  id: '',
  stringa: '',
  intero: '',
  lungo: '',
  flotto: '',
  doppio: '',
  grandecimale: '',
  datalocale: null,
  istante: null,
  zonadatatempo: null,
  booleano: false,
  enumeratore: '',
};

const validationSchema = Yup.object().shape({
  id: Yup.number(),
  stringa: Yup.string(),
  intero: Yup.number(),
  lungo: Yup.number(),
  flotto: Yup.number(),
  doppio: Yup.number(),
  grandecimale: Yup.number(),
  datalocale: Yup.date().nullable(),
  istante: Yup.date().nullable(),
  zonadatatempo: Yup.date().nullable(),
  booleano: Yup.boolean(),
  enumeratore: Yup.string(),
});

const formikBag = {
  mapPropsToValues: ({ alltywpsql }) => alltywpsql || emptyAlltywpsql,

  enableReinitialize: true,

  validationSchema,

  handleSubmit: (values, { setSubmitting, props: { onSubmit } }) => {
    onSubmit(values);
    setSubmitting(false);
  },

  displayName: 'AlltywpsqlForm',
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
  withFormik(formikBag)
)(AlltywpsqlForm);
