import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/alltywpsqls';

export const apiAlltywpsqlGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiAlltywpsqlPost = async (serviceUrl, alltywpsql) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: alltywpsql ? JSON.stringify(alltywpsql) : null,
  };
  return request(url, options);
};

export const apiAlltywpsqlPut = async (serviceUrl, id, alltywpsql) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: alltywpsql ? JSON.stringify(alltywpsql) : null,
  };
  return request(url, options);
};

export const apiAlltywpsqlDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
