import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,
  stringa: PropTypes.string,
  intero: PropTypes.number,
  lungo: PropTypes.number,
  flotto: PropTypes.number,
  doppio: PropTypes.number,
  grandecimale: PropTypes.number,
  datalocale: PropTypes.string,
  istante: PropTypes.string,
  zonadatatempo: PropTypes.string,
  booleano: PropTypes.bool,
  enumeratore: PropTypes.string,
});

export const formValues = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  stringa: PropTypes.string,
  intero: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  lungo: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  flotto: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  doppio: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  grandecimale: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  datalocale: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  istante: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  zonadatatempo: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  booleano: PropTypes.bool,
  enumeratore: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  stringa: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  intero: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  lungo: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  flotto: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  doppio: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  grandecimale: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  datalocale: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  istante: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  zonadatatempo: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  booleano: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  enumeratore: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  stringa: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  intero: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  lungo: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  flotto: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  doppio: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  grandecimale: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  datalocale: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  istante: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  zonadatatempo: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  booleano: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  enumeratore: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
